.bedroom-item {
	border: 1px solid var(--general-2);
	border-radius: 4px;
}

.add-btn {
	height: 40px;
	line-height: 40px;

	& * {
		vertical-align: middle;
	}

	input {
		height: 100%;
	}
}

.round-btn {
	width: 40px !important;
	height: 40px !important;
	box-shadow: unset;
	border-radius: 50% !important;
	min-width: unset !important;

	.mat-icon {
		margin-inline-start: auto !important;
		margin-inline-end: auto !important;
	}
}

.bed-icon {
	border-radius: 50%;
	width: 35px;
	height: 35px;
	background-color: var(--info-1);
	position: relative;
	display: inline-block;

	> .mat-icon {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
	}
}

.bedroom-count {
	line-height: 35px;
	color: var(--general-5);
}

.w-35px {
	width: 35px;
}

.euro-icon {
	height: 18px;
	width: 18px;
	font-size: 18px;
}

input.price-field::placeholder {
	color: var(--general-5) !important;
}
